import {Injectable} from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class ProvidersConfigService {

  private allProvidersEndpoint = null;

  public getAllProvidersEndpoint(): string {
    if (!this.allProvidersEndpoint) {
      console.warn('All Providers Endpoint is not set!');
    }
    return this.allProvidersEndpoint;
  }

  public setAllProvidersEndpoint(value: string): void {
    this.allProvidersEndpoint = value;
  }
}
