import { Injectable } from '@angular/core';
import {GroupOption} from './GroupOption';
import {PeraGraph} from 'ng-common-library';
import {AttendingProvider} from 'ng-common-library/lib/model/AttendingProvider';
import {ProviderFilterService} from '../filter/providers/provider-filter.service';
import {Subject} from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class ProviderGroupService implements GroupOption {

  /**
   * things that need to emit before generating the provider lanes
   */
  dependency = new Subject<void>();

  constructor(private providerService: ProviderFilterService) {
    this.providerService.providersLoaded.subscribe(() => {
      this.dependency.next();
    });
  }

  /**
   * generate group choice object for facilities
   */
  generateLanes(): { groupName: string, lanes: { laneFunction: (peraGraph: PeraGraph) => boolean, laneTitle: string }[] } {
    const option: { groupName: string, lanes: { laneFunction: (peraGraph: PeraGraph) => boolean, laneTitle: string }[] } = {
      groupName: 'Provider',
      lanes: []
    };
    for (const provider of this.providerService.providers) {
      option.lanes.push({
        laneFunction: this.generateProviderLaneFilterFunction(provider),
        laneTitle: provider.lastName + ', ' + provider.firstName
      });
    }
    return option;
  }

  /**
   * helper function for generating a filter function for provider groups given a provider
   * @param provider - provider to filter for
   */
  private generateProviderLaneFilterFunction(provider: AttendingProvider): (peraGraph: PeraGraph) => boolean {
    const firstName = provider.firstName;
    const lastName = provider.lastName;
    return function (peraGraph: PeraGraph): boolean {
      return peraGraph.attendingProvider.firstName === firstName && peraGraph.attendingProvider.lastName === lastName;
    };
  }

}
