import { Injectable } from '@angular/core';
import {GroupOption} from './GroupOption';
import {PeraGraph} from 'ng-common-library';

@Injectable({
  providedIn: 'root'
})
export class WarningCategoryGroupService implements GroupOption {

  /**
   * things that need to emit before generating the warning lanes
   */
  dependency = null;

  constructor() { }

  /**
   * generate group choice object for facilities
   */
  generateLanes(): { groupName: string, lanes: { laneFunction: (peraGraph: PeraGraph) => boolean, laneTitle: string }[] } {
    return {
      groupName: 'Warning Category',
      lanes: [{
        laneFunction: (peraGraph: PeraGraph) => peraGraph.activeWarning != null && peraGraph.activeWarning.category === 'V',
        laneTitle: 'Very High Warning'
      },
        {
          laneFunction: (peraGraph: PeraGraph) => peraGraph.activeWarning != null && peraGraph.activeWarning.category === 'H',
          laneTitle: 'High Warning'
        },
        {
          laneFunction: (peraGraph: PeraGraph) => peraGraph.activeWarning != null && peraGraph.activeWarning.category === 'M',
          laneTitle: 'Medium Warning'
        },
        {laneFunction: (peraGraph: PeraGraph) => peraGraph.activeWarning == null, laneTitle: 'No Warning'}]
    };

  }
}
