import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class FilterQueryConfigService {

  private filterQueryEndPoint = null;

  getFilterQueryEndPoint(): any {
    if (!this.filterQueryEndPoint) {
      console.warn('Filter query endpoint is not set!');
    }
    return this.filterQueryEndPoint;
  }

  setFilterQueryEndPoint(value: any) {
    this.filterQueryEndPoint = value;
  }
}
