import { Injectable } from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Observable} from 'rxjs';
import {ServiceLineConfigService} from './service-line-config.service';

@Injectable({
  providedIn: 'root'
})
export class ServiceLineRestService {

  constructor(private http: HttpClient, private serviceLineConfig: ServiceLineConfigService) { }

  /**
   * return all service line descriptions
   */
  getAllServiceLineDescriptions(): Observable<string[]> {
    return this.http.get<string[]>(this.serviceLineConfig.getServiceLineDescriptionsEndpoint());
  }
}
