import {Injectable} from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class ServiceLineConfigService {

  private serviceLineDescriptionsEndpoint = null;

  getServiceLineDescriptionsEndpoint(): any {
    if (!this.serviceLineDescriptionsEndpoint) {
      console.warn('Service Line Descriptions endpoint is not set!');
    }
    return this.serviceLineDescriptionsEndpoint;
  }

  setServiceLineDescriptionsEndpoint(value: any) {
    this.serviceLineDescriptionsEndpoint = value;
  }
}
