import {Injectable} from '@angular/core';
import {HttpClient} from "@angular/common/http";
import {AttendingProvider} from "ng-common-library/lib/model/AttendingProvider";
import {Observable} from "rxjs";
import {ProvidersConfigService} from './providers-config.service';

@Injectable({
    providedIn: 'root'
})
export class ProvidersApiService {

    constructor(private http: HttpClient, private providersConfigService: ProvidersConfigService) {
    }

    getAllProviders(): Observable<AttendingProvider[]> {
        return this.http.get<AttendingProvider[]>(this.providersConfigService.getAllProvidersEndpoint());
    }
}
