import { Injectable } from '@angular/core';
import {Diagnosis} from '../model/diagnosis';
import {HttpClient} from '@angular/common/http';
import {DiagnosisConfigService} from './diagnosis-config.service';
import {Observable, timer} from 'rxjs';
import {switchMap} from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class DiagnosisRestService {

  private pollInstanceDiagnoses$: Observable<any>;

  constructor(private http: HttpClient, private diagnosisConfigService: DiagnosisConfigService) {
    this.pollInstanceDiagnoses$ = timer(0, 90000);
  }

  /**
   * returns an observable with all diagnoses
   */
  getAllDiagnoses(): Observable<Diagnosis[]> {
    return this.http.get<Diagnosis[]>(this.diagnosisConfigService.getDiagnosesEndpoint() + '/all');
  }

  /**
   * returns an observable that broadcasts all active diagnoses on an interval
   */
  getAllDiagnosisInterval(): Observable<any> {
    return this.pollInstanceDiagnoses$.pipe(
      switchMap(() => this.getAllDiagnoses())
    );
  }

}
