import {EventEmitter, Injectable} from '@angular/core';
import {Diagnosis} from '../model/diagnosis';

@Injectable({
  providedIn: 'root'
})
export class DiagnosisFilterService {

  // diagnoses currently selected in sidebar
  public selectedDiagnoses = {};

  // all diagnoses to choose from
  public diagnoses: Diagnosis[] = [];

  // lets sidebar know when diagnoses have been returned from REST call
  public diagnosesLoaded$ = new EventEmitter<void>();

  // event for changing all checkbox values
  // true for select
  // false for deselect
  public toggleAll = new EventEmitter<boolean>();

  constructor() {
  }

}
