import {Injectable} from '@angular/core';
import {PeraGraph} from 'ng-common-library';
import {SortChoices} from './SortEnum';

@Injectable({
  providedIn: 'root'
})
export class SortService {
  // sort currently applied to array view
  public activeSortFunction: ((peraGraphA: PeraGraph, PeraGraphB: PeraGraph) => number) = null;

  // sort function in sidebar -> not currently active in array view
  private _stagedSortFunction: ((peraGraphA: PeraGraph, PeraGraphB: PeraGraph) => number) = null;

  activeSortName: string = null;

  stagedSortName: string = null;

  nonStandardSortFunctionActive = false;

  nonStandardSortFunction: ((peraGraphA: PeraGraph, PeraGraphB: PeraGraph) => number) = null;

  // options of dropdown
  choices: { value: { sortName: string, transformer: (peragraph: PeraGraph) => any }, description: string, tooltip: string }[] = [
    {
      value: {
        sortName: SortChoices.DisplayName, transformer: (peragraph: PeraGraph) => {
          return peragraph.displayName;
        }
      }, description: 'Patient Name', tooltip: 'Alphabetical sort by patient last name'
    },
    {
      value: {
        sortName: SortChoices.MedicalRecordNumber, transformer: (peragraph: PeraGraph) => {
          return peragraph.patient.medicalRecordNumber;
        }
      }, description: 'MRN', tooltip: 'Sort graphs by the medical record number of the patient'
    },
    {
      value: {
        sortName: SortChoices.VisitNumber, transformer: (peragraph: PeraGraph) => {
          return peragraph.vnm;
        }
      }, description: 'Visit Number', tooltip: 'Sort graphs by the visit number of the patient'
    },
    {
      value: {
        sortName: SortChoices.RIScore, transformer: (peragraph: PeraGraph) => {
          return peragraph.latestRi;
        }
      }, description: 'RI Score', tooltip: 'Sort by Rothman Index score value'
    },
    {
      value: {
        sortName: SortChoices.HospitalLOS, transformer: (peragraph: PeraGraph) => {
          return peragraph.admitDate;
        }
      }, description: 'Hospital LOS', tooltip: 'Sort by hospital length of stay'
    },
    {
      value: {
        sortName: SortChoices.WarningSeverity, transformer: (peragraph: PeraGraph) => {
          if (peragraph.activeWarning) {
            const category = peragraph.activeWarning.category;
            if (category === 'V') {
              return 4;
            } else if (category === 'H') {
              return 3;
            } else if (category === 'M') {
              return 2;
            } else {
              return 1;
            }
          }
          return null;
        }
      }, description: 'Warning Severity', tooltip: 'Sort by the severity of patient warnings'
    },
    {
      value: {
        sortName: SortChoices.WarningDuration, transformer: (peragraph: PeraGraph) => {
          if (peragraph.activeWarning) {
            return peragraph.activeWarning.createdDate;
          }
          return null;
        }
      }, description: 'Warning Created Time', tooltip: 'Sort by warning created time'
    }
  ];

  constructor() {
    this.stageSortFunction(this.choices[0].value.transformer, 1, this.choices[0].value.sortName);
    // set staged sort to active
    this.activeSortFunction = this.getStagedSortFunction();
    this.activeSortName = this.stagedSortName;
  }


  getStagedSortFunction(): (peraGraphA: PeraGraph, PeraGraphB: PeraGraph) => number {
    return this._stagedSortFunction;
  }

  /**
   * what to do if sidebar is destroyed
   */
  clearSort() {
    this.activeSortName = null;
    this.activeSortFunction = null;
    this.stagedSortName = null;
    this._stagedSortFunction = null;
  }

  /**
   * stage sort function for application
   */
  stageSortFunction(transformer: (peraGraphA: PeraGraph) => any, direction: number, sortName: string) {
    this.stagedSortName = sortName;
    this._stagedSortFunction = this.createSortFunction(transformer, direction);
  }

  /**
   * generates sort function from transformer function, direction, and sortName
   * @param transformer - function to take peraGraph and return primitive value for sorting
   * @param direction - direction of sort
   */
  createSortFunction(transformer: (peraGraphA: PeraGraph) => any, direction: number) {
    return function (peraGraphA: PeraGraph, peraGraphB: PeraGraph): number {
      // get comparable values from peragraphs to sort by
      const valueA = transformer(peraGraphA);
      const valueB = transformer(peraGraphB);
      let comparison = 0;
      // compare values and return
      if (valueA && valueB) {
        if (valueA > valueB) {
          comparison = 1;
        } if (valueA < valueB) {
          comparison = -1;
        }
      } else {
        if (valueA && !valueB) {
          comparison = 1;
        } else if (valueB && !valueA) {
          comparison = -1;
        }
      }
      return comparison * direction;
    };
  }
}
